<template>
  <div class="breadcrumb">
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item
        v-for="(item, index) in matched"
        :key="index"
        :to="item.path === path ? '' : { path: item.path }"
        >{{ item.meta.title }}</el-breadcrumb-item
      >
    </el-breadcrumb>
  </div>
</template>

<script>
export default {
  name: 'breadcrumb',
  data() {
    return {
      matched: [],
      path: ''
    }
  },
  watch: {
    '$route.fullPath': function (newV, oldV) {
      this.matched = this.$router.currentRoute.matched
      this.path = this.$router.currentRoute.path
      return newV
    }
  },
  mounted() {
    this.matched = this.$router.currentRoute.matched
    this.path = this.$router.currentRoute.path
  }
};
</script>

